@Composable
fun introductionItem(
    title: String,
    description: String,
){
    introductionItemTitle(
        title = title
    )
    introductionItemDescription(
        description = description
    )
    customDivider()
}

@Composable
fun introductionItemTitle(
    title: String,
){
    H3 {
        SpanText(
            text = title,
            modifier = Modifier
                .fontWeight(FontWeight.Bold)
        )
    }
}

@Composable
fun introductionItemDescription(
    description: String,
){
    SpanText(
        text = description,
        modifier = Modifier
            .lineHeight(1.6)
    )
}